.main-header {
	&__logo {
		font-size: 3em;
		font-family: "Bebas", sans-serif;
		color: $second-color;
		text-decoration: none;
		@include r(480) {
			margin-bottom: 40px; }
		span {
			color: $accent-color; } } }

.main-navigation {
	padding-bottom: 7px;
	li {
		display: inline-block;
		margin-right: 30px;
		@include r(480) {
			margin-right: 15px; }
		&:last-child {
			margin-right: 0; } }
	&__link {
		font-size: +fz(16);
		font-family: "Bebas", sans-serif;
		text-decoration: none;
		text-transform: uppercase;
		color: #b9b9b9;
		transition: color 0.5s ease;
		&:hover,
		&_active {
			color: $accent-color; } } }

.page-title,
.main-footer {
	position: relative;
	background-color: $accent-color;
	@include r(980) {
		margin-left: -10px;
		margin-right: -10px; }
	&:before,
	&:after {
		content: '';
		position: absolute;
		display: block;
		background-color: $second-color;
		width: 2000px;
		height: 100%;
		top: 0;
		@include r(980) {
			display: none; } }
	&:before {
		left: -2010px; }
	&:after {
		right: -2010px; } }

.page-title {
	margin-bottom: 20px;
	padding: 20px;
	font-size: +fz(24);
	color: #fff;
	@include r(980) {
		padding: 20px 10px; } }

.blog-wrapper {
	display: flex;
	-webkit-justify-content: space-between;
	justify-content: space-between;
	@include r(768) {
		-webkit-flex-direction: column;
		-ms-flex-direction: column;
		flex-direction: column; } }

.blog-posts-wrapper {
	width: 640px;
	@include r(900) {
		width: 580px; }
	@include r(830) {
		width: 500px; }
	@include r(768) {
		width: 100%; } }
.right-sidebar {
	width: 220px;
	@include r(768) {
		width: 100%;
		margin-bottom: 40px; } }

.blog-post {
	position: relative;
	&__icon {
		position: absolute;
		padding-top: 20px;
		width: 73px;
		height: 73px;
		left: -83px;
		text-align: center;
		background-color: $accent-color;
		font-size: 36px;
		@include r(1140) {
			display: none; } }
	&__slider {
		position: relative;
		margin-bottom: 25px;
		img {
			width: 100%; }
		.slick-arrow {
			position: absolute;
			top: 42%;
			cursor: pointer;
			height: 52px;
			width: auto;
			z-index: 1;
			opacity: 0.8;
			&_prev {
			 	left: 20px; }
			&_next {
			 	right: 20px; } } }
	&__info {
		float: left;
		margin-right: 50px;
		@include r(900) {
			float: none;
			width: 100%; }
		h6 {
			margin-bottom: 10px;
			font-size: 1em;
			color: $accent-color;
			@include r(900) {
				display: inline-block;
				margin-right: 10px; }
			@include r(480) {
				display: block; } }
		p {
			margin-bottom: 20px;
			font-size: 1em;
			color: #bababa;
			@include r(900) {
				display: inline-block;
				margin-right: 30px; }
			@include r(480) {
				display: block; } } }
	&__content {
		h2 {
			font-size: +fz(18);
			color: $accent-color; }
		p {
			margin: 25px 0;
			&:last-child {
				margin-bottom: 0; } } }
	&__img {
		margin-bottom: 25px;
		img {
			width: 100%; } }
	&__quote {
		position: relative;
		padding: 30px;
		padding-left: 75px;
		background-color: #F1F1F1;
		font-family: "HelveticaNeueItalic", sans-serif;
		@include r(320) {
			padding: 75px 30px 30px; }
		&:before {
			position: absolute;
			left: 35px;
			top: 19px;
			content: "\"";
			font-size: 72px;
			font-family: "GeorgiaRegular", sans-serif;
			color: $accent-color;
			@include r(320) {
				left: 45%; } } }

	&__title-link {
		font-size: 1ems;
		color: #a0a0a0;
		text-decoration: none; } }

.blog-post-separator {
	width: 100%;
	margin: 50px 0;
	img {
		width: 100%; } }

.blog-pagination {
	margin-bottom: 40px;
	@include r(768) {
		text-align: center; }
	&__link {
		display: inline-block;
		width: 26px;
		height: 26px;
		text-align: center;
		padding-top: 3px;
		font-size: 14px;
		font-family: "Bebas", sans-serif;
		text-decoration: none;
		color: #fff;
		background-color: $second-color;
		transition: background-color 0.5s ease;
		&_active,
		&:hover {
			background-color: $accent-color; } } }

.right-sidebar {
	overflow: hidden;
	&__title {
		position: relative;
		display: table;
		margin: 40px 0 20px;
		font-size: 14px;
		color: $accent-color;
		background-color: transparent;
		cursor: pointer;
		transition: all 0.5s ease;
		&:first-child {
			margin-top: 0; }
		&:before {
			position: absolute;
			content: ' ';
			top: 3px;
			left: 110%;
			width: 15px;
			height: 100%;
			background: url("../img/down-arrow.svg") no-repeat;
			background-size: contain;
			transition: all 0.5s ease; }
		&:after {
			position: absolute;
			width: 800px;
			height: 100%;
			content: ' ';
			left: 140%;
			background: url("../img/separator.png");
			-webkit-background-size: cover;
			-o-background-size: cover;
			background-size: cover; }
		&.hidden {
			margin-bottom: 0;
			&:before {
				top: -2px;
				transform: rotate(180deg); } } }
	&__categories-navigation {
		a {
			font-size: 1em;
			color: #464646;
			text-decoration: none; } }
	&__photo-gallery {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		grid-template-rows: 1fr 1fr;
		grid-column-gap: 6px;
		grid-row-gap: 5px;
		img {
			width: 100%;
			height: 100%; } } }

.main-footer {
	display: flex;
	padding: 40px;
	-webkit-justify-content: space-between;
	justify-content: space-between;
	color: #fff;
	@include r(980) {
		padding: 40px 10px; }
	@include r(680) {
		-webkit-flex-direction: column;
		-ms-flex-direction: column;
		flex-direction: column; }
	.column {
		width: 30%;
		@include r(680) {
			width: 100%;
			margin-bottom: 20px; } }
	&__widget-title {
		margin-bottom: 30px;
		font-size: 20px;
		&_no-title {
			margin-bottom: 50px; } }
	p {
		margin-bottom: 20px; }
	span {
		color: #f3b29d; }
	a {
		color: #fff;
		text-decoration: none; } }
