body {
	font-size: 12px;
	font-family: "HelveticaNeue", sans-serif;
	color: $font-color;
	background: url("../img/bg.jpg"); }

h1, h2, h3, h4, h5, h6 {
	font-family: "Bebas", sans-serif;
	font-weight: normal; }

.main-header {
	display: flex;
	padding: 23px 0;
	align-items: flex-end;
	justify-content: space-between;
	@include r(480) {
		-webkit-flex-direction: column;
		-ms-flex-direction: column;
		flex-direction: column;
		align-items: center; } }

.container {
	max-width: 960px;
	margin: 0 auto;
	@include r(980) {
		margin: 0 10px; } }

.out {
	overflow: hidden; }
